.conteiner {
  display: grid;
}
.title {
  color: #fff;
  text-align: center;
}
.galery_box {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  box-sizing: border-box;
  margin-bottom: 10px;
}
.galery_item {
  width: 440px;
  height: 250px;
  border-radius: 10px;
  margin-top: 10px;
}
.galery_item:hover {
  transform: scale(1.1);
  cursor: pointer;
}
